import React from 'react';
import PropTypes from 'prop-types';
import { platform, IOS } from '@vkontakte/vkui';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import PanelHeaderButton from '@vkontakte/vkui/dist/components/PanelHeaderButton/PanelHeaderButton';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import { FormLayout, FormLayoutGroup, Input } from '@vkontakte/vkui';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Div from '@vkontakte/vkui/dist/components/Div/Div';

const osName = platform();

const Persik = props => (
    <Panel id={props.id}>
        <PanelHeader
            left={<PanelHeaderButton onClick={props.go} data-to="home">
                {osName === IOS ? <Icon28ChevronBack /> : <Icon24Back />}
            </PanelHeaderButton>}
        >Конструктор команд</PanelHeader>
        <Group>
            <FormLayout>
                <FormLayoutGroup top="Создание новой команды">
                <Input type="text" placeholder="Название команды" />
                </FormLayoutGroup>
            </FormLayout>
        </Group>
        <Group>
			<Div><Button size="m" level="2" onClick={props.addElement} data-to="text">Текст</Button></Div>
            <Div><Button size="m" level="2" data-to="post">Пост</Button></Div>
            <Div><Button size="m" level="2" data-to="image">Изображение</Button></Div>
            <Div><Button size="m" level="2" data-to="video">Видео</Button></Div>
		</Group>
        <Group>
        <Div><Button size="l" level="2" data-to="add_command">Создать команду</Button></Div>
        </Group>
    </Panel>
);

Persik.propTypes = {
    id: PropTypes.string.isRequired,
    go: PropTypes.func.isRequired,
};

export default Persik;
